.card-company {
    width: 120px;
    height: 120px;
    margin: 0px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    flex-direction: column;
}

.card-company:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
}

.cadr-company-img {
    width: 100px;
    height: 90px;
    margin-top: 10px;
    border-radius: 4px;
    box-shadow: 0 6px 20px rgba(16, 16, 16, 0.2);
    background-color: #ffff;
    border: 1px solid #008647
}

.card-company-Link {
    font-size: small;
    color: #008647;
    text-decoration: none;
}