.company__section {
    padding: 10px 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #c7c7c7;
}

.company__wrapper {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.company__wrapper .btn {
    margin-bottom: 5px;
}

.company__container__heaiding {
    display: flex;
    flex-direction: row;
    float: right;
}

.company__container {
    box-shadow: 0 6px 20px rgba(5, 6, 9, 0.2);
    display: flex;
    overflow-x: auto;
    padding: 10px;
    border: none;
    border-radius: 4px;
    align-items: center;
    justify-content: center;
    width: 1000px;
    height: 220px;
    background-color: #ffff;
}

.company__heading {
    color: #1a1359;
    margin-bottom: 24px;
}

button.iLquPE:hover {
    background-color: #008647;
    color: #FFF;
}

button.lgksPe:hover {
    background-color: #008647;
    color: #FFF;
}

button.rec-arrow-right:hover {
    background-color: #008647;
}

button.rec-dot {
    background-color: #1a1359;
    box-shadow: 0 0 1px 3px #1a1359;
}

button.rec-dot:hover,
button.rec-dot:active,
button.rec-dot:focus {
    box-shadow: 0 0 1px 3px #828282;
}

@media screen and (max-width: 960px) {
    .company__container {
        width: 700px;
    }
    .company__heading {
        font-size: 20px;
    }
}

@media screen and (max-width: 768px) {
    .company__container {
        width: 500px;
    }
    .company__heading {
        font-size: 20px;
        margin-left: 30px;
    }
}

@media screen and (max-width: 530px) {
    .company__container {
        width: 300px;
    }
    .company__heading {
        font-size: 20px;
    }
}