:root {
    --primary: #008647;
    --yellow: #fbf000;
}

.btn {
    padding: 8px 20px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #FFF;
    border: 1px solid var(--primary)
}

.btn--outline {
    background-color: transparent;
    color: #008647;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--outline--yellow {
    background-color: transparent;
    color: #fbf000;
    padding: 8px 20px;
    border: 1px solid var(--yellow);
    transition: all 0.3s ease-out;
}

.btn-medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--large--y {
    padding: 12px 26px;
    font-size: 20px;
}

.btn-medium--y {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--medium--y:hover,
.btn--large--y:hover {
    background-color: #fbf000;
    color: #008647;
    transition: all 0.3s ease-out;
}

.btn--medium:hover,
.btn--large:hover {
    background: #008647;
    color: #FFF;
    transition: all 0.3s ease-out;
}